.DoubleCard::after {
  content: "";
  position: absolute;
  top: 10px;
  left: 10px;
  width: 50px;
  height: 50px;
  background-color: #ca0100;
  border-radius: 5px;
  box-shadow: -5px -5px 0 0 white;
}

